
import Vue from 'vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import VueHtml5Editor from 'vue-html5-editor'
import axios from 'axios'
import 'font-awesome/css/font-awesome.min.css'
import { getItem } from '../../utils/storage'

export default {
name: 'h5Editor',
data () {
return {
item: {},
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
show: false,
opt: 0,
userId: getItem('user').id
}
},
components: {
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio
},
mounted () {
const { item } = this.$route.params
this.item = item || {
title: '',
content: '',
type: 0,
fileType: 'html',
fileId: ''
}
console.log(this.item)
},
methods: {
updateData (e = '') {
this.item.content = e
},
saveFile () {
this.show = !this.show
console.log(this.item)
const body = {
id: this.userId,
title: this.title === '' ? '无标题' : this.title,
...this.item,
type: this.opt
}
axios
.post(`/api/savefile`, body)
.then((res) => {
res.data && this.$router.push({
name: 'noteList'
})
}).catch((error) => {
console.log(error)
})
}
}
}
Vue.use(VueHtml5Editor, {

name: 'vue-html5-editor',


showModuleName: false,


icons: {
text: 'fa fa-pencil',
color: 'fa fa-paint-brush',
font: 'fa fa-font',
align: 'fa fa-align-justify',
list: 'fa fa-list',
link: 'fa fa-chain',
unlink: 'fa fa-chain-broken',
tabulation: 'fa fa-table',
image: 'fa fa-file-image-o',
hr: 'fa fa-minus',
eraser: 'fa fa-eraser',
undo: 'fa-undo fa',
'full-screen': 'fa fa-arrows-alt',
info: 'fa fa-info'
},


image: {

sizeLimit: 512 * 1024,


upload: {
url: null,
headers: {},
params: {},
fieldName: {}
},



compress: {
width: 1600,
height: 1600,
quality: 80
},


uploadHandler (responseText) {

var json = JSON.parse(responseText)
if (!json.ok) {
alert(json.msg)
} else {
return json.data
}
}
},


language: 'zh-cn',

i18n: {

'zh-cn': {
'align': '对齐方式',
'image': '图片',
'list': '列表',
'link': '链接',
'unlink': '去除链接',
'table': '表格',
'font': '文字',
'full screen': '全屏',
'text': '排版',
'eraser': '格式清除',
'info': '关于',
'color': '颜色',
'please enter a url': '请输入地址',
'create link': '创建链接',
'bold': '加粗',
'italic': '倾斜',
'underline': '下划线',
'strike through': '删除线',
'subscript': '上标',
'superscript': '下标',
'heading': '标题',
'font name': '字体',
'font size': '文字大小',
'left justify': '左对齐',
'center justify': '居中',
'right justify': '右对齐',
'ordered list': '有序列表',
'unordered list': '无序列表',
'fore color': '前景色',
'background color': '背景色',
'row count': '行数',
'column count': '列数',
'save': '确定',
'upload': '上传',
'progress': '进度',
'unknown': '未知',
'please wait': '请稍等',
'error': '错误',
'abort': '中断',
'reset': '重置'
}
},


hiddenModules: [],



visibleModules: [
'text',
'color',
'font',
'align',
'list',
'link',
'unlink',
'tabulation',
'image',
'hr',
'eraser',
'undo',
'full-screen',
'info'
],


modules: {

}
})



